/**
 * It add different type of audio's required in the game and play only the background image.
 */
const addMusic = function () {

  this.game.backgroundScore = this.game.add.audio('backgroundScore',
    this.game.angularContainer.volume * this.soundVolumeMultipliers.backgroundScore, true);

  this.game.spinSound = this.game.add.audio('spinSound',
    this.game.angularContainer.volume * this.soundVolumeMultipliers.spinSound);

  this.game.winSound = this.game.add.audio('winSound',
    this.game.angularContainer.volume * this.soundVolumeMultipliers.winSound);

  this.game.reelStopSound = this.game.add.audio('reelStopSound',
    this.game.angularContainer.volume * this.soundVolumeMultipliers.reelStopSound);

  this.game.buttonClickSound = this.game.add.audio('buttonClickSound',
    this.game.angularContainer.volume * this.soundVolumeMultipliers.buttonClickSound);

  this.game.scatterBackgroundScore = this.game.add.audio('ScatterBackgroundScore',
    this.game.angularContainer.volume * this.soundVolumeMultipliers.ScatterBackgroundScore, true);

  this.game.ScatterStopSound = this.game.add.audio('ScatterStopSound',
    this.game.angularContainer.volume * this.soundVolumeMultipliers.ScatterStopSound);

  // play background Sound
  this.game.backgroundScore.play();
}

/**
 * The function toggel the music/audio when clicked on volume button
 */
const toggleMute = function () {
  if (this.game.angularContainer.muteAudio) {
    this.volume = 0;
    this.game.sound.volume = this.volume;
  } else {
    this.volume = 1;
    this.game.sound.volume = this.volume;
  }
}

export { addMusic, toggleMute };



