/**
 * The function define for draw panel.
 * add the images to pannel area(bottom area of main screen)
 */
const drawPanels = function () {
  const HeaderY = this.dimensions.reel.reelY - this.game.cache.getImage('header-panel').height;
  this.game.cockpitHeader = this.game.add.image(0, HeaderY, 'header-panel');

  const imageHeight = (this.dimensions.reel.reelY + (3 * this.dimensions.reel.tileWidth));
  this.game.cockpitPanel = this.game.add.image(0, imageHeight, 'footer-panel');


};
/**
 * The function define for add the button to the panel area(bottom area of main screen),
 * design all the panel area, set the font
 */
const addButtons = function () {
  this.game.counter = 0;
  this.game.spinButton = this.game.add.button(
    this.coordinates.statsBlock.spinButton.x,
    this.coordinates.statsBlock.spinButton.y,
    'spinButton', this.spin, this, 1, 0, 2);
  this.game.spinButton.anchor.set(0.5);
  this.game.maxBetButton = this.game.add.button(
    this.coordinates.statsBlock.maxBetButton.x,
    this.coordinates.statsBlock.maxBetButton.y,
    'maxBetButton', this.spin, this, 1, 0, 2);
  this.game.autoPlayButton = this.game.add.button(
    this.coordinates.statsBlock.autoPlayButton.x,
    this.coordinates.statsBlock.autoPlayButton.y,
    'autoplayButton', this.spin, this, 1, 0, 2);
  this.game.betButtonFrame = this.game.add.image(
    this.coordinates.statsBlock.betButtonFrame.x,
    this.coordinates.statsBlock.betButtonFrame.y,
    'betButton-frame');
  this.game.levelButtonFrame = this.game.add.image(
    this.coordinates.statsBlock.levelButtonFrame.x,
    this.coordinates.statsBlock.levelButtonFrame.y,
    'levelButton-frame');
  this.game.coinButtonFrame = this.game.add.image(
    this.coordinates.statsBlock.coinButtonFrame.x,
    this.coordinates.statsBlock.coinButtonFrame.y,
    'coinButton-frame');
  this.game.coinValueButtonFrame = this.game.add.image(
    this.coordinates.statsBlock.coinValueButtonFrame.x,
    this.coordinates.statsBlock.coinValueButtonFrame.y,
    'coinValueButton-frame');
  this.game.spinButtonTransparentFrame = this.game.add.image(
    this.coordinates.statsBlock.spinButton.x,
    this.coordinates.statsBlock.spinButton.y,
    'spinButtonTransparentFrame');
  this.game.spinButtonTransparentFrame.anchor.set(0.5);
  this.game.spinButtonRotationalFrame = this.game.add.image(
    this.coordinates.statsBlock.spinButton.x,
    this.coordinates.statsBlock.spinButton.y,
    'spinButtonRotationalFrame');
  this.game.spinButtonRotationalFrame.anchor.set(0.5);
  this.game.spinButtonRotationalFrame.alpha = 0.2;
  this.game.levelTextButton = this.game.add.text(
    this.coordinates.statsBlock.levelText.x,
    this.coordinates.statsBlock.levelText.y,
    this.stats.intialLevel);
  this.game.levelTextButton.stroke = this.styles.statsBlock.label.textColor;
  this.game.levelTextButton.strokeThickness = 1;
  this.game.levelTextButton.fontSize = this.styles.statsBlock.label.fontSize;
  this.game.levelTextButton.fill = this.styles.statsBlock.label.textColor;
  this.game.levelTextButton.anchor.set(0.5);
  this.game.totalCoinsButton = this.game.add.text(
    this.coordinates.statsBlock.totalCoinsText.x,
    this.coordinates.statsBlock.totalCoinsText.y,
    this.stats.totalCoins);
  this.game.totalCoinsButton.fontSize = this.styles.statsBlock.label.fontSize;
  this.game.totalCoinsButton.fill = this.styles.statsBlock.label.textColor;
  this.game.totalCoinsButton.anchor.set(0.5)
  this.game.totalCoinsButton.stroke = this.styles.statsBlock.label.textColor;
  this.game.totalCoinsButton.strokeThickness = 1;
  this.game.coinValueText = this.game.add.text(
    this.coordinates.statsBlock.coinValueText.x,
    this.coordinates.statsBlock.coinValueText.y,
    this.stats.IntialCoinValue);
  this.game.coinValueText.stroke = this.styles.statsBlock.label.textColor;
  this.game.coinValueText.strokeThickness = 1;
  this.game.coinValueText.fontSize = this.styles.statsBlock.label.fontSize;
  this.game.coinValueText.fill = this.styles.statsBlock.label.textColor;
  this.game.coinValueText.anchor.set(0.5);
  this.game.betTextButton = this.game.add.text(
    this.coordinates.statsBlock.betText.x,
    this.coordinates.statsBlock.betText.y,
    this.stats.bet);
  this.game.betTextButton.fontSize = this.styles.statsBlock.label.fontSize;
  this.game.betTextButton.fill = this.styles.statsBlock.label.textColor;
  this.game.betTextButton.anchor.set(0.5);
  this.game.betTextButton.stroke = this.styles.statsBlock.label.textColor;
  this.game.betTextButton.strokeThickness = 1;
  this.game.levelIncreaseButton = this.game.add.image(
    this.coordinates.statsBlock.levelIncreaseButton.x,
    this.coordinates.statsBlock.levelIncreaseButton.y,
    'rightArrow');
  this.game.levelIncreaseButton.scale.setTo(0.9);
  this.game.levelDecreaseButton = this.game.add.image(
    this.coordinates.statsBlock.levelDecreaseButton.x,
    this.coordinates.statsBlock.levelDecreaseButton.y,
    'leftArrow');
  this.game.levelDecreaseButton.scale.setTo(0.9);
  this.game.levelDecreaseButton.alpha = 0.5;
  this.game.coinValueIncreaseButton = this.game.add.image(
    this.coordinates.statsBlock.coinValueIncreaseButton.x,
    this.coordinates.statsBlock.coinValueIncreaseButton.y,
    'rightArrow');
  this.game.coinValueIncreaseButton.scale.setTo(0.9);
  this.game.coinValueDecreaseButton = this.game.add.image(
    this.coordinates.statsBlock.coinValueDecreaseButton.x,
    this.coordinates.statsBlock.coinValueDecreaseButton.y,
    'leftArrow');
  this.game.coinValueDecreaseButton.scale.setTo(0.9);
  this.game.coinValueDecreaseButton.alpha = 0.5;
  this.game.imageAutoplay = this.game.add.text(this.coordinates.statsBlock.imageAutoplay.x,
    this.coordinates.statsBlock.imageAutoplay.y, 'AUTOPLAY');
  this.game.imageAutoplay.font = 'avqest';
  this.game.imageAutoplay.fill = this.styles.statsBlock.label.textColor;
  this.game.imageAutoplay.anchor.set(0.7);
  this.game.imageAutoplay.scale.setTo(0.7);

  this.game.imageMaxBet = this.game.add.text(this.coordinates.statsBlock.imageMaxBet.x,
    this.coordinates.statsBlock.imageMaxBet.y, 'MAX BET');
  this.game.imageMaxBet.font = 'avqest';
  this.game.imageMaxBet.fill = this.styles.statsBlock.label.textColor;
  this.game.imageMaxBet.anchor.set(0.5);
  this.game.imageMaxBet.scale.setTo(0.7);

  this.game.imageBet = this.game.add.text(this.coordinates.statsBlock.imageBet.x,
    this.coordinates.statsBlock.imageBet.y, 'BET');
  this.game.imageBet.font = 'avqest';
  this.game.imageBet.fill = this.styles.statsBlock.label.textColor;
  this.game.imageBet.anchor.set(0.5);
  this.game.imageBet.scale.setTo(0.7);

  this.game.imageLevel = this.game.add.text(this.coordinates.statsBlock.imageLevel.x,
    this.coordinates.statsBlock.imageLevel.y, 'LEVEL');

  this.game.imageLevel.font = 'avqest';
  this.game.imageLevel.fill = this.styles.statsBlock.label.textColor;
  this.game.imageLevel.anchor.set(0.5);
  this.game.imageLevel.scale.setTo(0.7);

  this.game.imageCoinValue = this.game.add.text(this.coordinates.statsBlock.imageCoinValue.x,
    this.coordinates.statsBlock.imageCoinValue.y, 'COIN VALUE');

  this.game.imageCoinValue.font = 'avqest';
  this.game.imageCoinValue.fill = this.styles.statsBlock.label.textColor;
  this.game.imageCoinValue.anchor.set(0.5);
  this.game.imageCoinValue.scale.setTo(0.6);

  this.game.imageCoins = this.game.add.text(this.coordinates.statsBlock.imageCoins.x,
    this.coordinates.statsBlock.imageCoins.y, 'COIN');

  this.game.imageCoins.font = 'avqest';
  this.game.imageCoins.fill = this.styles.statsBlock.label.textColor;
  this.game.imageCoins.anchor.set(0.5);
  this.game.imageCoins.scale.setTo(0.7);
  // The button deals with the paytable functionality.
  this.game.infoButton = this.game.add.sprite(
    this.coordinates.statsBlock.infoButton.x,
    this.coordinates.statsBlock.infoButton.y,
    'info');
  this.game.infoButton.anchor.set(0.5);
  this.game.infoButton.scale.setTo(0.7);
};

export { drawPanels, addButtons };



