import * as Phaser from 'phaser-ce';
import LoadingState from './states/loading-state';
import TrailerState from './states/trailer-state';
import MainScreenState from './states/main-screen-state';
import JackpotState from './states/jackpot-state';
import { Router } from '@angular/router';

/** The class initiate the game and provide different states of the game */
export default class GameStateProvider {

  /** Use to define the instance of type LoadingState */
  private _loadingState: any;

  /** Use to define the instance of type TrailerState*/
  private _trailerState: any;

  /** Use to define the instance of type MainScreenState */
  private _mainScreenState: any;

  /** Use to define the instance of type JackpotState*/
  private _jackpotState: any;

  /**  An object of Router type.*/
  router: Router;

  /** Define an array of states the game will have */
  states: any[];

  /** An object of Http type. */
  httpService: any;

  /**
   * Perform the intialisation task
   * @param _httpService An object of Http type.
   * @param _router An object of Router type.
   *
   */
  constructor(_httpService, _router) {

    this.states = [];
    this.httpService = _httpService;
    this.router = _router;
  }

  /**
   * It intiate all the states of the game and push the states in state[] array
   */
  initiateStates() {
    this._loadingState = new LoadingState(this.httpService, this.router);
    this._trailerState = new TrailerState(this.httpService);
    this._mainScreenState = new MainScreenState(this.httpService);
    this._jackpotState = new JackpotState(this.httpService);

    this.states.push(this._loadingState);
    this.states.push(this._trailerState);
    this.states.push(this._mainScreenState);
    this.states.push(this._jackpotState);
  }

  /**
   *
   * @return All the states the game will probably eneter into.
   *
   */
  getStates(): any[] {
    return this.states;
  }

}








