import { Component, OnInit, ElementRef, Output, EventEmitter } from '@angular/core';
import { CreateAccountService } from './create-account.service';
import { SharedService } from '../shared/shared.service';
import ComponentToggleService from './../contracts/component-toggel-service';
import { FormControl, FormGroup, Validators, FormsModule, FormBuilder } from '@angular/forms';
import { PasswordCustomValidation } from './password-custom-validator';
import { successPopUpToast, popUpToast } from './../shared/pop-ups';
import { AppConfig } from './../app.config';

@Component({
  selector: 'app-create-account',
  templateUrl: './create-account.component.html',
  styleUrls: ['./create-account.component.css'],
  providers: [CreateAccountService]
})

/**
 * Define the create account section through which user can sign in
 */
export class CreateAccountComponent implements OnInit {
  /** An object of CreateAccountService type.*/
  singUpService: CreateAccountService;

  /** Define the firstName that the user provide */
  firstName: string;
  /** Define the lastName that the user provide */
  lastName: string;
  /** Define the userName that the user provide */
  userName: string;
  /** Define the emailId that the user provide */
  emailId: string;
  /** Define the password that the user provide */
  password: string;
  /** Define the confirmPassword that the user provide */
  confirmPassword: string;

  /** An object of SharedService type used for toggling the contenet of different panels.*/
  closeForm: SharedService;

  /** Used for creating instance of Appconfig.*/
  config: AppConfig;

  /** Used for creating an instance of FormGroup for interaction with HTML template */
  signUpForm: FormGroup;

  /** Used for emitting the closing status of the form which is listened by login component */
  @Output() formSubmitted = new EventEmitter<boolean>();

  /**
   * Create a new CreateAccountService instance.
   *
   * @param _singUpService An object of CreateAccountService type.
   * @param _closeForm An object of SharedService type used for toggling the contenet of different panels.
   * @param _formBuilder An object of FormBuilder type used for creating form and validations
   *
   */
  constructor(private _singUpService: CreateAccountService,
    private _closeForm: SharedService,
    private _formBuilder: FormBuilder) {
    this.singUpService = _singUpService;
    this.closeForm = _closeForm;
    this.config = new AppConfig();

  }

  /**
  * Define different validations for each field that are part of the form.
  */
  ngOnInit() {
    const emailRegex = /^\w+([\.-]?\w+)*@\W*(gmail)\W*(\.\w{2,3})+$/i;
    this.signUpForm = this._formBuilder.group({
      'firstName': ['', [Validators.required, Validators.minLength(3)]],
      'lastName': ['', [Validators.required, Validators.minLength(3)]],
      'userName': ['', [Validators.required, Validators.minLength(3)]],
      'emailId': ['', [Validators.required, Validators.minLength(3), Validators.pattern(emailRegex)]],
      'passwords': this._formBuilder.group({
        'password': ['', [Validators.required, Validators.minLength(5)]],
        'confirmPassword': ['', [Validators.required, Validators.minLength(5)]]
      }, { validator: PasswordCustomValidation.MatchPassword })
    });

  }

  /**
   * Call the singup service API and depending upon the response subcribe accordingly.
   * Success: Generate a userCreatedSuccess popup toast and reset the form
   * Error: Genearte a error popup toast.
   *
   * @returns void.
   *
   */
  submitForm() {
    // this.singUpService.signup(this.firstName, this.lastName, this.userName, this.emailId, this.password)
    //   .subscribe(
    //   (response) => {
        successPopUpToast(this.config.StringConstants.userCreatedSuccess, this.config.Timeouts.toastDuration);
        this.signUpForm.reset()

        // @parameters =>transactionHistory, promotionLinks, accountDetails,articlesList,projectList,regulationList,
        // responsibleGamingList, createAccountPopup,rightPanel)
        const componentVisibilityObject = new ComponentToggleService(true, true, true, true, true, true, true, false);
        this.closeForm.toggelDisplayPanel(componentVisibilityObject);
    //   },
    //   (error) => {
    //     this.signUpForm.reset()
    //     popUpToast(this.config.StringConstants.apiDefaultErrorMsg, this.config.Timeouts.toastDuration)
    //   },
    // );
    this.formSubmitted.next(true);

  }
}



