class PostSpinDTO {

  bet: number;
  level: number;
  coinValue: number;
  totalCoins: number;
  totalCash: number;
  gameId: number;
  cheatSymbolArray: number[];
  jackpotSymbol: string;

  constructor(
    gameId,
    bet,
    level,
    coinLevel,
    calculateTotalCash,
    IntialCoinValue,
    cheatDetails
  ) {
    this.bet = bet * level;
    this.level = level;
    this.coinValue = coinLevel;
    this.totalCoins = Math.round(calculateTotalCash / IntialCoinValue);
    this.totalCash = calculateTotalCash;
    this.gameId = gameId;
    this.cheatSymbolArray = [];
  }

}

export default PostSpinDTO;



