class ComponentToggleService {

    transactionHistory: boolean;
    promotionLinks: boolean;
    accountDetails: boolean;
    articlesList: boolean;
    projectList: boolean;
    regulationList: boolean;
    responsibleGamingList: boolean;
    rightPanel: boolean;

    constructor(transactionHistory, promotionLinks, accountDetails,
        articlesList,
        projectList,
        regulationList,
        responsibleGamingList,
        rightPanel) {
        this.transactionHistory = transactionHistory;
        this.promotionLinks = promotionLinks;
        this.accountDetails = accountDetails;
        this.articlesList = articlesList;
        this.projectList = projectList;
        this.regulationList = regulationList;
        this.responsibleGamingList = responsibleGamingList;
        this.rightPanel = rightPanel;
    }
}
export default ComponentToggleService;



