import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Rx';
import { Subject } from 'rxjs/Subject';

@Injectable()
export class SharedService {
    private subject = new BehaviorSubject<Object>({
        'transactionHistory': true,
        'promotionLinks': true,
        'accountDetails': true,
        'articlesList': true,
        'projectList': true,
        'regulationList': true,
        'responsibleGamingList': true,
        'rightPanel': false,
    });
    private loginSubject = new BehaviorSubject<string>('');

    constructor() { }

    /**
     *
     * @param transactionSharedObject
     */
    toggelDisplayPanel(transactionSharedObject: Object) {
        this.subject.next(transactionSharedObject);
    }

    /**
     *
     */
    observableToggelDisplayPanel(): Observable<any> {
        return this.subject.asObservable();
    }

    /**
     *
     * @param loginMessage
     */
    showLoginerror(loginMessage: string) {
        this.loginSubject.next(loginMessage);

    }

    /**
     *
     */
    displayLoginError(): Observable<any> {
        return this.loginSubject.asObservable();
    }


}








