
import { Component } from '@angular/core'
import * as Phaser from 'phaser-ce';
import GameState from './../../../../../contracts/phaser/game-state';
import * as JackpotPageModule from './drawJackpotPage';
import * as JackpotSpin from './spinJackpotReel';
import * as JackpotPageWinAnimation from './jackpotPage-Win-Animations';
import { GamePageService } from './../../../game-page/game-page.service';

/**
 * The class define for design and handle the events of JackpotState
 */
export default class JackpotState implements GameState {

    /** An object of httpService. used for httpService request*/
    private httpService;

    /** An object for gameData*/
    gameData: any;

    /** Define whether state is intial or not */
    isInitial: boolean;

    /** Define the name of state */
    name: string;

    /** Define an object for assests of this state  */
    stateAssets: any;

    /** An object of Game type. Game is an interface that define the basic structure of the game */
    game: any;

    /** Define an object for reel Data */
    reelData: any;

    /** Define whether jackopt or not */
    isJackpot: boolean;

    /** Define the y co-ordinates of win syhmbol in reel  */
    winSymbolY: number;

    /** Define the reel  after slow down and finally stoped */
    isReelFinalStop: boolean;

    /** Define an array object for position of symbols in reel */
    helperArray: number[];

    /** Define an mapper array for map the win symbol to corresponding win amount in reel*/
    x: Map<string, number>;

    /** Define an object for spin-reel data from backend */
    spinData: any;

    /** Define an object for all the methods */
    methods: any;

    /**
    * Create an instance of JackpotState and initialize the class properties.
    * @param _httpservice  An object of GamePageService type.
     */
    constructor(private _httpservice) {
        this.name = 'Jackpot';
        this.httpService = _httpservice;
        this.isReelFinalStop = false;
        this.winSymbolY = -1;
        this.isInitial = false;
        this.stateAssets = {
            jackpotSpinButton: {}
        }
        this.reelData = {
            number: 0,
            isSpinning: false,
            flag: 0,
            remValue: 0,
            spinSpeed: 0,
            spinTimeStamp: 0,
            counter: 0
        }
        this.methods = {};
        this.helperArray = [235, 115, 955, 835, 715, 595, 475, 355];
        this.x = new Map<string, number>();
        this.x.set('JA', 0);
        this.x.set('JB', 1);
        this.x.set('JC', 2);
        this.x.set('JD', 3);
        this.x.set('JE', 4);
        this.x.set('JF', 5);
        this.x.set('JG', 6);
        this.x.set('JH', 7);
    }

    /**
     *  This function is called first. It should contain code to handle the loading of assets needed by your game
     */
    preload() {
    }

    /**
     * The create function is called automatically once the preload has finished.
     * Set the visibility of test panel and activate current test panel.
     * Call the method which is required for design the state.
     * Bind all the necessary methods.
     */
    create() {
        this.game.angularContainer.isTestPanelHidden = true;
        this.game.angularContainer.isJackpotTestModeActive = true;
        JackpotPageModule.initializationVariable.apply(this);
        JackpotPageModule.drawBackground.apply(this);
        JackpotSpin.spinReel.apply(this);
        this.methods.moveToPreviousState = JackpotSpin.moveToPreviousState.bind(this);
        this.methods.JackpotPageWinAnimation = JackpotPageWinAnimation.symbolWin.bind(this);
        this.methods.jackpotPageWinText = JackpotPageWinAnimation.showWin.bind(this);
    }

    /**
     * The update (and render) functions are called every frame.
     * Resposible for reel spinning.
     */
    update() {
        if (!this.isReelFinalStop) {
            JackpotSpin.spinAnimate.apply(this);
        }
    }
}



