const spinButtonAnimation = function () {
  this.game.spinButtonBallAnimation = this.game.add.sprite(this.coordinates.statsBlock.spinButton.x,
  this.coordinates.statsBlock.spinButton.y, 'spinButtonBall-particle', 8);
  this.game.emitterAnimation = this.game.add.emitter(this.coordinates.statsBlock.spinButton.x,
  this.coordinates.statsBlock.spinButton.y, 4);
  this.game.spinButtonBallAnimation.animations.add('pulse');
  this.game.spinButtonBallAnimation.play('pulse', 30, true);
  this.game.spinButtonBallAnimation.anchor.set(0.5);
  this.game.spinButtonBallAnimation.scale.setTo(1.2);
  this.game.spinButtonBallAnimation.alpha = 0;
  const spinButtonAnimationTween = this.game.add.tween(this.game.spinButtonBallAnimation).to(
    { alpha: 0.9 }, 100, Phaser.Easing.Sinusoidal.In, true
  );

  this.game.emitterAnimation.makeParticles('spinButton-particle', [0, 1, 2, 3]);
  this.game.emitterAnimation.setRotation(360, 360);
  this.game.emitterAnimation.setAlpha(0.1, 0.5);
  this.game.emitterAnimation.setScale(0.5, 0.5);
  this.game.emitterAnimation.gravity = -20;

  // false means don't explode all the sprites at once, but instead release at a rate of one particle per 100ms
  // The 5000 value is the lifespan of each particle before it's killed
  this.game.emitterAnimation.start(false, 200, 20);
  spinButtonAnimationTween.onComplete.add(() => {
    this.game.add.tween(this.game.spinButtonBallAnimation).to({ alpha: 0 }, 1000, Phaser.Easing.Sinusoidal.In, true);
    this.methods.removeSpinButtonAnimation();
  })

}
const removeSpinButtonAnimation = function () {
  this.game.emitterAnimation.on = false;
}

const spinButtonFrameAnimation = function () {
  this.game.spinButtonRotationalFrame.angle -= 1;
}
export { spinButtonAnimation, spinButtonFrameAnimation, removeSpinButtonAnimation}



