/**
 * The function define for update the reels data with jackpot symbol
 * and update the jackpot array
 */
const updateReelsWithjackpotSymbol = function () {
    this.jackpotArray = [];
    // Calculations for each reel.
    for (const reel of this.stateAssets.reels) {
        let preIndex = Number.MIN_SAFE_INTEGER;
        let isjackpotSymbolPresent = false;
        this.spinResponse.randomNumbers.forEach((item, index) => {
            index = index + 1;
            if ((index > ((reel.number - 1) * 3)) && (index <= (reel.number * 3)) && (item === 'Z')) {
                isjackpotSymbolPresent = true;
                if (index - preIndex > 2) {
                    preIndex = index;
                    this.jackpotArray.push(preIndex);
                }
            }
        });
    }
}

export { updateReelsWithjackpotSymbol };



