import { Injectable, ErrorHandler } from '@angular/core';
import { popUpToast } from './pop-ups';
import { AppConfig } from './../app.config';

@Injectable()
export default class NGPErrorHandler extends ErrorHandler {

  constructor() {
    // The true paramter tells Angular to rethrow exceptions,
    // so operations like 'bootstrap' will result in an error
    // when an error happens. If we do not rethrow, bootstrap will always succeed.
    super(true);
  }

  handleError(error) {

    const config = new AppConfig();
    popUpToast(config.StringConstants.unexpectedError, config.Timeouts.toastDuration);
    // TODO:send the error to the server
    // delegate to the default handler
    super.handleError(error);
  }

}



