import { Component, OnInit } from '@angular/core'
import * as Phaser from 'phaser-ce';
import GameState from './../../../../../contracts/phaser/game-state';
import GameInfo from './../../../../../contracts/phaser/game-info';
import * as Shared from './../../../../../shared';
// state modules
import * as BackgroundModule from './backgroud-renderer';
import * as BetLineModule from './bet-lines';
import * as configureModule from './configure';
import * as ReelsModule from './reel-renderer';
import * as PanelModule from './panel-renderer';
import * as EventHandlingModule from './event-handling';
import * as SpinModule from './spin';
import * as JackpotCounterModule from './jackpot-counter';
import * as SoundsModule from './sounds';
import * as EventListenerModule from './event-listner';
import * as UpdateStatisticsModule from './update-statistics';
import * as WinAnimationsModule from './win-animations';
import * as JackpotStateBacklogModule from './jackpot-state-backlog';
import * as spinButtonAnimationModule from './spin-button-animation';
import * as EnableDisableModule from './enable-disable-controls';
import * as AutoPlay from './auto-play';
import * as ScatterModule from './scatter';
import * as JackpotSymbolModule from './jackpot-symbol';
import { AppConfig } from './../../../../../app.config';

/**
 * The class defines the MainScreen State.
 * It will design the state and handle all the event for MainScreen state.
 */
export default class MainScreenState implements GameState, OnInit {

  /** An object of httpService. used for httpService request*/
  httpService;

  /** Define for style for different text, betlines and lines */
  styles: any;

  /** Define different coordinates  for different images*/
  coordinates: any;

  /** Define different dimensions for stateblock, betlines, spin*/
  dimensions: any;

  /** Define the name of state */
  name: string;

  /** Define whether state is intial or not */
  isInitial: boolean;

  /** Define an object for assests of this state  */
  stateAssets: any;

  /** An object of Game type. Game is an interface that define the basic structure of the game */
  game: any;

  /** Define volume for audio track  */
  volume: number;

  /** Define for store Spin result */
  spinResponse: any;

  /** Define an object for hold all the methods */
  methods: any;

  /** An object for gameData*/
  gameData: GameInfo;

  /** Define an object for store winAnimations assets */
  winAnimations: any;

  /** Define the auto play running status when auto play is active*/
  isAutoPlayRunning: boolean;

  /** Define number of free spin pending in case of scatter */
  freeSpinsPending: number;

  /** Define for store scatter response before free spinning start */
  lastResponseBeforeFreeSpins: any;

  /** Define the scatter is running in case of free spin.  */
  isScatterRunning: boolean;

  /** Define array for contains scatter sysmbol */
  scatterArray: any;

  /** Define for check jackpot win or not */
  isJackpot: boolean;

  /** Define for contains jackpout amount */
  jackpotCounter: number;

  /**  Define object for contains Appconfig object*/
  config: AppConfig;

  /**
   * Represents the main-state of the slot game. The game-screen on which reels are visible and slot game is played.
   *  @param _httpservice An object of GamePageService type
   */
  constructor(_httpService) {

    this.name = 'MainScreen';
    this.isInitial = false;
    this.jackpotCounter = 0;
    this.coordinates = {};
    this.config = new AppConfig();
    this.stateAssets = {
      isWinAnimationOver: false,
      isAutoSpinActive: false, // to be used later
      ready: false,
      colors: null,
      point: null,
      group: null,
      reels: [],
      reelCount: 5,
      randomNumbers: [],
      betLinesRight: [12, 18, 20, 16, 11, 14, 15, 17, 13, 19],
      betLinesLeft: [4, 2, 6, 8, 1, 10, 9, 7, 3, 5],
      betLinesCoordinates: [],
      tileCoordinates: [],
      betLineDetails: [],
      winAnimationTime: 2
    }
    this.volume = 1;
    this.httpService = _httpService;
    this.methods = {};
    this.winAnimations = {};
    this.isAutoPlayRunning = false;
    this.freeSpinsPending = -1;
    this.lastResponseBeforeFreeSpins = {};
    this.isScatterRunning = false;
    this.scatterArray = [];
  }

  ngOnInit() {
  }

  /**
   * Define method for which is responsible for call jackpotHeader method for set the value of jackpot counter.
   */
  getJackpotCash() {
    JackpotCounterModule.jackpotHeader.apply(this);
  }

  /**
   *  This function is called first. It should contain code to handle the loading of assets needed by your game
   */
  preload() {
  };

  /**
   * The create function is called automatically once the preload has finished.
   * set the visibility of test panel and activate current test panel.
   * bind all the methods which is required further.
   * draw the state using drawbackground, drawpanel
   * set all the designs for main screen state and attached all the animations.
   */
  create() {
    this.game.angularContainer.isJackpotTestModeHidden = true;
    this.game.angularContainer.isJackpotTestModeActive = false;
    this.methods.removeWinAnimations = WinAnimationsModule.removeWinAnimations.bind(this);
    this.methods.showWinAnimations = WinAnimationsModule.showWinAnimations.bind(this);
    this.methods.jackpotWinAnimation = WinAnimationsModule.jackpotWinAnimation.bind(this);
    this.methods.spinButtonAnimation = spinButtonAnimationModule.spinButtonAnimation.bind(this);
    this.methods.removeSpinButtonAnimation = spinButtonAnimationModule.removeSpinButtonAnimation.bind(this);
    this.methods.levelDecrease = UpdateStatisticsModule.levelDecrease.bind(this);
    this.methods.levelIncrease = UpdateStatisticsModule.levelIncrease.bind(this);
    this.methods.coinValueDecrease = UpdateStatisticsModule.coinValueDecrease.bind(this);
    this.methods.coinValueIncrease = UpdateStatisticsModule.coinValueIncrease.bind(this);
    this.methods.winCalculations = UpdateStatisticsModule.winCalculations.bind(this);
    this.methods.enableButtonsOnSpin = EnableDisableModule.enableButtonsOnSpin.bind(this);
    this.methods.disableButtonsOnSpin = EnableDisableModule.disableButtonsOnSpin.bind(this);
    this.methods.hidePopUps = EnableDisableModule.hidePopUps.bind(this);
    this.methods.popUpMessageGenerator = Shared.popUpMessageGenerator.bind(this);
    this.methods.toggleMute = SoundsModule.toggleMute.bind(this);
    this.methods.externalEventListner = EventListenerModule.listenExternal.bind(this);
    this.methods.autoSpin = EventHandlingModule.spinClick.bind(this);
    this.methods.autoplayClickDownEventListner = AutoPlay.autoPlay.bind(this);
    this.methods.resetAutoPlaySettings = AutoPlay.resetAutoPlaySettings.bind(this);
    this.methods.triggerPendingSpins = AutoPlay.triggerPendingSpins.bind(this);
    this.methods.revertScatterWinAnimations = WinAnimationsModule.revertScatterWinAnimations.bind(this);
    this.methods.tweekReelWithRandom = EventHandlingModule.tweekReelWithRandom.bind(this);
    this.methods.updateReelsWithScatterFlags = ScatterModule.updateReelsWithScatterFlags.bind(this);
    this.methods.updateReelsWithJackpotFlags = JackpotSymbolModule.updateReelsWithjackpotSymbol.bind(this);
    this.methods.JackpotStateBacklog = JackpotStateBacklogModule.showpendingJackpotAnimations.bind(this);
    configureModule.configure.apply(this);
    BackgroundModule.drawBackground.apply(this);
    BetLineModule.configureBetLines.apply(this);
    BetLineModule.drawBetLines.apply(this);
    ReelsModule.drawReels.apply(this);
    PanelModule.drawPanels.apply(this);
    PanelModule.addButtons.apply(this);
    EventHandlingModule.bindSpinButton.apply(this);
    EventHandlingModule.bindSpaceBarToSpin.apply(this);
    EventHandlingModule.bindInfoButton.apply(this);
    SoundsModule.addMusic.apply(this);
    WinAnimationsModule.drawWinToast.apply(this);
    EventHandlingModule.bindAutoplayButton.apply(this);
    EventHandlingModule.bindMaxBetButton.apply(this);
    EventHandlingModule.bindLevelIncreaseButton.apply(this);
    EventHandlingModule.bindLevelDecreaseButton.apply(this);
    EventHandlingModule.bindCoinValueIncreaseButton.apply(this);
    EventHandlingModule.bindCoinValueDecreaseButton.apply(this);
    EventHandlingModule.bindStatsBlock.apply(this);
    JackpotCounterModule.addJackpotHeader.apply(this);

    setInterval(() => {
      this.getJackpotCash();
    }, this.config.Timeouts.jackpotCashInterval);
    this.game.black = this.game.add.image(0, 0, 'black');
    this.game.black.alpha = 1;
    this.game.add.tween(this.game.black).to({ alpha: 0 }, 2 * Phaser.Timer.SECOND, Phaser.Easing.Linear.None, true);

    if (this.game.angularContainer.isJackpotState) {
      this.methods.JackpotStateBacklog();
    }
  };

 /**
  * The update (and render) functions are called every frame.
  * Resposible for reel spinning.
  * Define the animation on spin button.
  * Remove the diamond animation when they collide.
  */
  update() {
    SpinModule.spin.apply(this);
    spinButtonAnimationModule.spinButtonFrameAnimation.apply(this);
    if (this.game.winEmitter) {
      this.game.physics.arcade.collide(this.game.winEmitter);
    }
  };

  // render() {

  //    this.game.debug.start(450, 500, 'yellow');
  //      this.game.debug.line('mouse x');
  //      this.game.debug.line('mouse y');
  //      this.game.debug.stop();
  //      this.game.debug.start(550, 500, 'yellow');
  //      this.game.debug.line(":");
  //      this.game.debug.line(":");
  //      this.game.debug.stop();
  //      this.game.debug.start(560, 500, 'yellow');
  //      this.game.debug.line(this.game.input.mousePointer.x);
  //      this.game.debug.line(this.game.input.mousePointer.y);
  //      this.game.debug.stop();

  // };

}



