import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from './login/login.component';
import { HeaderComponent } from './header/header.component';
import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { SlotGameListComponent } from './slot-game-list/slot-game-list.component';
import { GamePageComponent as SpaceStorm } from './games/space-storm/game-page/game-page.component';
import { GamePageComponent as Chess } from './games/chess/game-page/game-page.component';
import { ErrorComponent } from './error/error.component';

/**
 * Define the route configurations.Depending upon the URL the respective component is rendered by mapping the path and URL.
 */
export const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'slot-game/space-storm', component: SpaceStorm },
  { path: 'slot-game/frozen-knights', component: Chess },
  { path: 'error', component: ErrorComponent },
  { path: 'slot-game/gamelist', component: SlotGameListComponent },
  { path: 'slot-game', component: SlotGameListComponent },
];

// Deprecated provide
// export const APP_ROUTER_PROVIDERS = [
//   provideRouter(routes)
// ];

export const routing: ModuleWithProviders = RouterModule.forRoot(routes);



