/**
 * The function is responsible for moving the reel along y-axis
 */
const spinAnimate = function () {
  for (const reel of this.stateAssets.reels) {
    reel.y += reel.spinSpeed;
    if (reel.y === this.dimensions.spin.reelHeight) {
      reel.y = -this.dimensions.spin.reelTopOffset;
    }
  }
};

/**
 * The function handle
 * 1.the speed of the reels
 * 2.in case the reels have scatter symbol a different audio is played.
 * 3. if there is a win it initiaite the win animations and calculate the win
 * 4.Enable/disable the control by checking the auto spinning and scatter is running or not
 */
const varySpeed = function () {
  for (const reel of this.stateAssets.reels) {
    const maxSpeed = 50;   // 0 + 2500/50
    if (reel.isSpinning) {
      reel.children.forEach(child => {
        const reelDownEffect = this.game.add.tween(child).to({ angle: +this.dimensions.spin.dropSpinAngle },
          50, Phaser.Easing.Cubic.In, true, 0, 2);
      });
      if (reel.spinTimeStamp < 100) {
        reel.spinTimeStamp += 5;
        reel.spinSpeed = -reel.spinTimeStamp / 50;
      } else if (reel.spinTimeStamp < 2500) {
        reel.spinTimeStamp += 25;
        reel.spinSpeed = reel.spinTimeStamp / 50;
      }
    } else {
      if (reel.spinSpeed > 0) {


        reel.spinTimeStamp = 0;
        reel.spinSpeed = 0;
        reel.children.forEach(child => {
          child.angle = -this.dimensions.spin.dropSpinAngle;
          const reelDropEffect = this.game.add.tween(child).to({ angle: this.dimensions.spin.dropSpinAngle },
            500, Phaser.Easing.Cubic.In, true, 0, 0);
          reelDropEffect.onComplete.add(() => {
            this.game.add.tween(child).to({ angle: 0 }, 300, Phaser.Easing.Cubic.In, true, 0, 0)
          })
        });
        let reelDropTween;
        if (reel.isScatterSoundNeeded) {
          reelDropTween = this.game.add.tween(reel).to({ y: this.dimensions.spin.reelYBounce },
            Phaser.Timer.SECOND * reel.tweenLag, Phaser.Easing.Linear.None, true);
        } else {
          reelDropTween = this.game.add.tween(reel).to({ y: this.dimensions.spin.reelYBounce },
            Phaser.Timer.SECOND * reel.tweenLag, Phaser.Easing.Bounce.Out, true);
        }
        reelDropTween.onComplete.add(() => {
          if (reel.isScatterSoundNeeded) {
            this.game.ScatterStopSound.play();
          } else {
            this.game.reelStopSound.play();
          }
          if (reel.number === this.stateAssets.reelCount) {
            this.game.ScatterStopSound.stop();
            // handling the spin response
            if (this.spinResponse.gameId) {
              this.methods.winCalculations(this.spinResponse);
              this.methods.showWinAnimations(this.spinResponse);
            }
            if (!this.isAutoPlayRunning && !this.isScatterRunning) {
              this.methods.enableButtonsOnSpin();
            } else {
              this.methods.disableButtonsOnSpin();
            }
          }
        });
      }
    }
  }
};

/**
 * The function intiate the spinning of reels, movement of the reels
 */
const spin = function () {
  const boundSpinAnimate = spinAnimate.bind(this);
  boundSpinAnimate();
  const boundVarySpeed = varySpeed.bind(this);
  boundVarySpeed();
}
export { spin };



